import { Avatar} from "@mui/material";
import {useEffect} from "react"
const PotetielChats = ({potentielsChats,creatChat})=>{
    useEffect(()=>{

      const carousel = document.querySelector(".carousel");
      const arrowsBtns = document.querySelectorAll(".potentiel-chats i");
      arrowsBtns.forEach((btn) => {
      btn.addEventListener("click", (e) => {
      if (e.target.id === "left") {
      carousel.scrollLeft -= 10;
      } else {
      carousel.scrollLeft += 10;
      }
      });
      });
      let isDragging = false,
      startX,
      startScrollLeft;
      const dragstart = (e) => {
      isDragging = true;
      carousel.classList.add("dragging");
      startX = e.pageX;
      startScrollLeft = carousel.scrollLeft;
      };
      const dragstop = () => {

      isDragging = false;
      carousel.classList.remove("dragging");
      };
      const dragging = (e) => {
      if (!isDragging) return;
      carousel.scrollLeft = startScrollLeft - (e.pageX - startX);
      };
      carousel.addEventListener("mousedown", dragstart);
      carousel.addEventListener("mousemove", dragging);
      document.addEventListener("mouseup", dragstop);
  },[])
  return(
   <div className="potentiel-chats">
   <i className="ri-arrow-drop-right-line" id="left"></i>
   <div className="carousel mouving">
   {
potentielsChats?.map((user,index)=>{
  return<Avatar src={user?.picture} alt="heloo" name="profile" key={index} className="avatar" onClick={()=>creatChat(user?._id)}/>
})
   }
   </div>
   <i className="ri-arrow-drop-left-line" id="right"></i>
  </div>
  )
}

export default PotetielChats;  