import React ,{useContext,useState,useRef}from "react";
import {AuthContext} from "../commponents/context/AutContext.jsx"
import {ChatContext} from "../commponents/context/ChatContext.jsx"
import {Button} from "react-bootstrap"
import "../styles/messenger.css"
import UserChat from "../commponents/Chats/UserChat"
import ChatBox from "../commponents/Chats/ChatBox"
import PotetielChats from "../commponents/Chats/PotetielChats"
import Helmet from "../commponents/Helmet/Helmet";
import InputEmoji from "react-input-emoji"
const Messenger = ({ socket }) => {
const {userChats,isUserChatsLoading,userChatsError,potentielsChats,creatChat,
updatecurrentChat,currentChat,messages,
isMessageLoading,sendTexMessage,onlineUsers,notifications,
markThisNotificationAsRead,InputRef
} = useContext(ChatContext)
const {user} = useContext(AuthContext)
const [textMessage,setTextMessage]=useState("")
  return (
    <>
    <Helmet title={"messenger"}>
          <div className="main-container">
           {/* ===========left part==============================================*/}
            <div className="left-container" ref={InputRef}>
              {/* ===========headt==============================================*/}
                <div className="header">
                  <div className="user-img">
                    <img className="dp" src={user?.picture} alt="heloo"/>
                   </div>
                  <div className="nav-icons">
                    <li><i className="ri-group-line"></i></li>
                    <li><i className="ri-chat-2-line">
                     </i></li>
                    <li><i className="ri-more-2-fill"></i></li>
                  </div>
                </div>

                  {/* ===========potetion chat==============================================*/}
                <PotetielChats potentielsChats={potentielsChats} creatChat={creatChat}/> 
                  

                 {/* ===========search bar==============================================*/}
                  <div className="search-container">
                      <div className="input">
                          <i className="ri-search-line"></i>
                        <input type="text" placeholder="Search or start new chat   "/>
                      </div>
                       <i className="ri-more-fill"></i>
                  </div> 

        {/*chats - list*/}
        <div className="chat-list">
      
        {
          userChats?.map((chat,index)=>{
            return(<div key={index}  onClick={()=>updatecurrentChat(chat)}>
                      <UserChat user={user} chat={chat} notifications={notifications}
                       markThisNotificationAsRead={markThisNotificationAsRead}
                       currentChat={currentChat}
                       />
                 </div>) 
          })
        }
      </div>
          </div>

    {/* ===========ligth part==============================================*/}
        <div className="right-container">
        {
          !currentChat && <p style={{textAlign:"center",width:"100%",justifyContent:"center",display:"flex",fontSize:"2rem",height:"100%"}}>no conversation selected yet......</p>
        }
        {
      currentChat &&
      <>
       <ChatBox user={user} 
       currentChat={currentChat} messages={messages} 
       isMessageLoading={isMessageLoading} onlineUsers={onlineUsers} updatecurrentChat={updatecurrentChat}/>
    {/*=======================message writting place==========================*/}
      <div className="chatbox-input">
        <i className="ri-attachment-2"></i>
        <i className="ri-mic-line"></i>
        <InputEmoji value={textMessage} onChange={setTextMessage} fontFamily="nonito"/>
        <Button onClick={()=>sendTexMessage(textMessage,currentChat,setTextMessage)}>send</Button>
      </div>
      </>
    }
    </div>
    </div>
    </Helmet>
    </>
  );
};
export default Messenger;
